.hero {
  height: 400px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2, .hero h3 {
  color: white;
}

.hero h1 {
  font-size: 60px;
  padding-top: 140px;
  margin-top: 0;
}

h1.thankYou{
  padding-top: 25px;
}

button.returnHome{
  left: 0;
  top: 30px;
}
button.returnHomeBtn{
  left: 180px;
  top: -70px;
}


